.assortment {
	&__title {
		position: absolute;
		top: vw(25);
		left: vw(0);
		color: #fff;
		width: 110%;
		span {
			color: rgba(255, 255, 255, 0.30);
		}
		@include max(md) {
			position: relative;
			top: auto;
			left: auto;
			margin-bottom: vw2(20);
			width: 100%;
		}
	}
	&__section {
			position: absolute;
			top: 0;
			left: 0;
			height: auto;
			z-index: 25;
			overflow: hidden;
			background: #07171D;
			@include max(md) {
					position: relative;
					height: auto;
					min-height: auto;
					top: auto;
					left: auto;
					padding-top: vw2(40);
					padding-bottom: vw2(40);
					padding-left: vw2(0);
					padding-right: vw2(0);
			}
			&__bg {
					position: absolute;
					left: vw(-30);
					display: flex;
					justify-content: flex-start;
					align-items: flex-start;
					top: vw(787);
					width: vw(728);
					height: vw(283);
					opacity: 0.2;
					img {
							position: relative;
							width: 100%;
							display: block;
							object-fit: left top;
							object-position: center center;
					}
			}
			.layout--trio {
					&__col {
							flex-flow: column nowrap;
							padding-bottom: vw(249);

							&:nth-child(1) {
									padding-top: vw(30);
									// @include max(md) {
									//     display: none;
									// }
							}
							&:nth-child(2) {
									@include max(md) {
											padding-bottom: 0;
									}
									
							}
							&:nth-child(3) {
									padding-top: vw(424);
									@include max(md) {
											padding-top: 0;
											padding-bottom: 0;
									 }
									
							}
					}
			}
			.container {
					z-index: 5;
			}
	}
	&__btn {
			position: absolute;
			bottom: -100vh;
			top: auto;
			left: vw(25);
			z-index: 26;
			&--start {
					@include max(md) {
							display: none;
					}
					&.active {
							display: none;
					}
			}
			&--finish {
					display: none;
					bottom: 1.71875vw;
					left: vw(0);
					&.active {
							display: flex;
					}
			}

	}
}